
#root {overflow:hidden;}
html, body, #root {
	width: 100%;
	height: 100%;
	margin: 0;
}
article {
	width: 100%;
	height: 100%;
	position:absolute;
}

article > h1 {
	font-size: 4rem;
	position: relative;
	top: 25%;
	left: 70%;
	transform: translate(-50%, -50%);
}

.Home {
	background-image: linear-gradient(135deg,#a6c0fe,#f68084);
	color: #fff;
	clip-path: polygon(0% 0%, 100% 16%, 100% 100%, 72% 100%, 0 85%);
}
.Home > h1 {
	font-size: 4rem;
	position: relative;
	top: 25%;
	left: 70%;
	transform: translate(-50%, -50%);
}
.Skill {
	background-image: linear-gradient(135deg,#a18cd1,#fbc2eb);
	color:#fff;
	clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);
}
.Skill > canvas {
	width: 100%;
	height: 100%;
	position: absolute;
}
.Skill > canvas > h1 {
	font-size: 4rem;
	position: relative;
	top: 25%;
	left: 70%;
	transform: translate(-50%, -50%);
}
.Project {
	background-image: linear-gradient(135deg,#ff9a9e,#fecfef);
	color: #fff;
	clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
}
.transitions-wrapper {
  position: relative;
  overflow: hidden;
  height: calc(var(--vh, 1vh) * 100);
  width: 100vw;
}
.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 1000ms ease-in-out;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 1000ms ease-in-out;
}

.topNavbar{
	top:2%;
	right:2%;
	height:	7%;
	width: 100%;
	position:fixed;
	z-index: 99;
	background-color: inherit;
	align-items: center;
	text-align: right;
}

.topNavbar > a{
	margin: 1rem;
	font-size: 1.5rem;
	font-weight: bold;
	justify-content: center;
	text-decoration: none;
	color: black;
}